import { NOTES_LIST_FAIL, NOTES_LIST_REQUEST, NOTES_LIST_SUCCESS } from "../constents/notesConstants";

export const noteListReducer = (state = {notes:[]},action)=>{
switch (action.type){
    case NOTES_LIST_REQUEST:
        return {loading:true};
        case NOTES_LIST_SUCCESS:
            return{loading: false,notes: action.playload};
            case NOTES_LIST_FAIL:
                return{loading:false,error:action,playload};

                default:
                    return state;
}
};